import React from "react";
import Head from "next/head";
import Link from "next/link";
import axios from "axios";

import Navbar from "../components/navbar"; 

import "../css/styles.scss";

export default class extends React.Component {
  static async getInitialProps() {
    const api =
      "https://api.football-data.org/v2/competitions/ELC/standings?standingType=HOME";
    const res = await axios.get(api, {
      headers: { "x-auth-token": `bb59bf677688476183511abee982ecd3` }
    });
    return { data: res.data.standings };
  }

  render() {
    const logoStyle = {
      width: "30px"
    };

    return (
      <div className="app-container">
        <Head>
          <title> League Table </title>
          <meta
            name="viewport"
            content="initial-scale=1.0, width=device-width"
          />
          <link
            rel="stylesheet"
            href="https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css"
          />
        </Head>
        <div className="container-fluid">
        <Navbar />
          <div className="row">
            <div className="col-sm-2" />
            <div className="col-sm-8">
              <h1> Futbol âš½ League Table </h1>
              <table className="table">
                <thead>
                  <tr>
                    <th>Position</th>
                    <th>Team</th>
                    <th>P</th>
                    <th>GL</th>
                    <th>W</th>
                    <th>D</th>
                    <th>L</th>
                  </tr>
                </thead>
                {this.props.data.map(standings => {
                  return (
                    <tbody>
                      {standings.table.map((value, index) => {
                        return (
                          <tr key={index}>
                            <td>{value.position}</td>
                            <td>
                              <img
                                style={logoStyle}
                                src={value.team.crestUrl}
                              />
                            </td>
                            <td>{value.points}</td>
                            <td>{value.playedGames}</td>
                            <td>{value.won}</td>
                            <td>{value.draw}</td>
                            <td>{value.lost}</td>
                          </tr>
                        );
                      })}
                    </tbody>
                  );
                })}
              </table>
            </div>
            <div className="col-md-2" />
          </div>
        </div>
      </div>
    );
  }
}

